<template>
    <div class="views news">
        <!-- navigate -->
        <mu-appbar class="appbar navigator" :style="{zIndex: region.visible ? 8000 : 1}">
            <mu-button slot="left" color="#fff" @click="$router.back()" icon>
                <mu-icon value="navigate_before"/>
            </mu-button>
            <mu-button color="#fff" @click="region.visible = true" style="width: 100%; font-size: 15px"
                       :disabled="$route.query.type_name !== '航道通告'"
                       flat>
                <span style="color: #fff">
                    {{$route.query.type_name === '航道通告' ? region.active.name : $route.query.type_name ? $route.query.type_name : '通告'}}
                </span>
                <mu-icon value="arrow_drop_down" v-if="$route.query.type_name === '航道通告'"/>
            </mu-button>
            <mu-button slot="right" color="transparent" icon>
                <mu-icon value="add"/>
            </mu-button>
        </mu-appbar>
        <!-- navigate -->
        <div class="header chip">
            <div class="head-item" v-for="item in header"
                 @click="goTypeList(item)">
                <span :class="item.icon" class="news-icon"
                      :style="{
                          borderColor: item.color,
                          backgroundColor: item.id === typeId ? '#fff' : item.color,
                          color: item.id === typeId ? item.color : '#fff',
                          boxShadow: `0 0 10px -2px ${item.color}`
                      }"></span>
                <span class="name theme-text-secondary">{{item.typeName}}</span>
            </div>
        </div>
        <div class="scroll-wrapper">
            <mu-load-more :refreshing="refreshing" :loading="loading" ref="content"
                          @refresh="refresh"
                          @load="load">
                <template v-if="list.length">
                    <mu-ripple class="new border-1px-b" v-for="(item, index) in list" :key="index"
                               @click="handlerNewItem(item, index, $route.query.type_name)">
                        <div class="new-pic" :style="{backgroundImage: `url(${item.pic})`}" v-if="item.pic"></div>
                        <div class="new-content">
                            <div class="title">{{item.title}}</div>
                            <div class="info">
                                <div>{{item.publisher}}</div>
                                <div>{{item.createTime | formatPast}}</div>
                            </div>
                        </div>
                    </mu-ripple>
                </template>
                <no-success :text="`暂时没有查询到《${$route.query.type_name || '新闻资讯'}》列表`" height="60vh" v-else/>
            </mu-load-more>
        </div>

        <!--<actionsheet v-model="active" :actions="actions" cancel-text="取消" @select="onSelect" @cancel="onCancel"/>-->
        <mu-popover class="news-popover" :open.sync="region.visible">
            <div class="list">
                <div class="item border-1px-b" :class="{active: item.id === region.active.id}"
                     v-for="item in region.list"
                     @click="handlerToggle(item)">
                    {{item.name}}
                </div>
            </div>
        </mu-popover>

        <transition name="fade-left">
            <keep-alive>
                <router-view class="views"/>
            </keep-alive>
        </transition>
    </div>
</template>

<script>
    import Navigate from "../../base/navigate/navigate"
    import NoSuccess from "../../base/no-success/no-success"
    import Shelter from '../../base/shelter'
    // import Actionsheet from 'vant/lib/actionsheet'
    // import 'vant/lib/actionsheet/style'
    import {formatPast} from "../../common/utils/time"
    import {
        // getNewHeader,
        getNewList,
        getByType,
        getbyarea
    } from "../../api/news"
    import {queryTypeDictByFid} from "../../api/search"
    import {isArray} from "../../types/index"

    export default {
        name: "news",
        data() {
            return {
                /***
                 * 区域选择器
                 */
                region: {
                    visible: false,
                    list: [],
                    active: {
                        name: '全部',
                        id: '029001'
                    }
                },
                /***
                 * 导航栏样式
                 */
                // color: "#fff",
                // shadow: false,
                // bgc: "transparent",
                // scrollTop: 0,
                /***
                 * 下拉刷新&上拉加载
                 */
                refreshing: false,
                loading: false,
                /***
                 * 搜索条件
                 */
                params: {
                    pageNumber: 0,
                    pageSize: 15,
                },
                /***
                 * 头部导航列表
                 */
                header: [
                    {
                        id: "001",
                        typeKey: "scale",
                        typeName: "航道尺度",
                        typeState: "1",
                        icon: ['GDHDT-icons-news-scale'],
                        activeIcon: require('../../../static/icon/news_scale_current.png'),
                        color: '#30C7D1'
                    },
                    {
                        id: "002",
                        typeKey: "weather",
                        typeName: "航道天气",
                        typeState: "1",
                        icon: ['GDHDT-icons-news-weather'],
                        activeIcon: require('../../../static/icon/news_weather_current.png'),
                        color: '#EF7272'
                    },
                    {
                        id: "003",
                        typeKey: "notice",
                        typeName: "航道通告",
                        typeState: "1",
                        icon: ['GDHDT-icons-news-notice'],
                        activeIcon: require('../../../static/icon/news_notice_current.png'),
                        color: '#72BD3E'
                    },
                    {
                        id: "004",
                        typeKey: "news",
                        typeName: "航道新闻",
                        typeState: "1",
                        icon: ['GDHDT-icons-news-new'],
                        activeIcon: require('../../../static/icon/news_news_current.png'),
                        color: '#F19F20'
                    },
                    {
                        id: "005",
                        typeKey: "law",
                        typeName: "法律法规",
                        typeState: "1",
                        icon: ['GDHDT-icons-news-law'],
                        activeIcon: require('../../../static/icon/news_law_current.png'),
                        color: '#AD8566'
                    }
                ],
                /***
                 * 搜索结果列表
                 */
                list: []
            }
        },
        computed: {
            typeId() {
                return this.$route.query.type_id
            }
        },
        async created() {
            this.region.list = await queryTypeDictByFid('029')

            this.region.active.id = this.region.list[0].id
            this.region.active.name = this.region.list[0].name
        },
        activated() {
            this.refresh()
        },
        methods: {
            /***
             * 点击头部导航按钮
             * @param type  => 类型
             */
            goTypeList(type) {
                if (this.$route.query.type_id === type.id) {
                    this.$router.replace(`/news`)
                } else {
                    this.$router.replace(`/news?type_id=${type.id}&type_name=${type.typeName}`)
                }

                this.region.active.id = this.region.list[0].id
                this.region.active.name = this.region.list[0].name

                this.refresh()
            },
            // 点击单条新闻
            handlerNewItem(item, index, typeName) {
                this.$router.push({
                    name: `news-detail`,
                    params: {
                        id: item.id,
                        typeName: typeName
                    }
                })
            },
            // /***
            //  * 顶部导航样式调整
            //  */
            // onScroll() {
            //     const top = this.$refs.scroll.scrollTop
            //     const max = this.$refs.header.offsetHeight - 40
            //
            //     if (top > max) {
            //         this.color = "#fff"
            //         this.bgc = ''
            //         this.shadow = true
            //     } else {
            //         this.color = "#fff"
            //         this.bgc = "transparent"
            //         this.shadow = false
            //     }
            // },
            // 刷新

            // TODO: refresh page data.
            refresh() {
                this.refreshing = true
                this.$refs.content.scrollTop = 0

                this.getNewList()
            },
            // TODO: pull reload page data.
            async load() {
                this.loading = true
                this.params.pageNumber++

                if (this.$route.query.type_id) {
                    this.params.typeId = this.$route.query.type_id

                    if (this.$route.query.type_name === '航道通告') {
                        const params = Object.assign({}, this.params, {areaId: this.region.active.id})

                        this.list.push(...isArray(await getbyarea(params)))
                    } else {
                        this.list.push(...isArray(await getByType(this.params)))
                    }
                } else {
                    this.params.typeId = undefined

                    this.list.push(...isArray(await getNewList(this.params)))
                }
                this.loading = false
            },
            /***
             * 获取导航列表
             */
            async getNewList() {
                if (this.$route.query.type_id) {
                    this.params.typeId = this.$route.query.type_id
                    this.params.pageNumber = 0

                    if (this.$route.query.type_name === '航道通告') {
                        const params = Object.assign({}, this.params, {areaId: this.region.active.id})

                        this.list = isArray(await getbyarea(params))
                    } else {
                        this.list = isArray(await getByType(this.params))
                    }
                } else {
                    this.params.typeId = undefined
                    this.params.pageNumber = 0

                    this.list = isArray(await getNewList(this.params))
                }

                this.refreshing = false
                this.$toast.clear()
            },

            // 选择
            handlerToggle(item) {
                this.region.visible = false
                this.region.active.id = item.id
                this.region.active.name = item.name

                this.getNewList()
            },
            // onCancel() {
            //     this.active = false
            // }
        },
        watch: {
            'region.visible'(value) {
                if (value) {
                    Shelter.show({
                        onClose: () => {
                            this.open = false
                        },
                        element: this.$el,
                        zIndex: 900
                    })
                } else {
                    Shelter.close()
                }
            }
        },
        filters: {
            formatPast: formatPast
        },
        components: {
            NoSuccess,
            Navigate,
            // Actionsheet
        }
    };
</script>

<style lang="stylus">
    @import '../../common/stylus/variable.styl';

    .news
        display flex
        flex-direction column
        padding-top 46px
        background-color #fff
        overflow hidden

        > .appbar
            position absolute
            top 0
            left 0
            z-index 999
            width 100%
            height 46px
            box-shadow none
            background-color transparent
            transition all 1s

            .mu-appbar-title
                display flex
                align-items center
                justify-content center
                line-height 46px
                height 100%

        > .header
            display flex
            align-items center
            width 100%
            height 90px
            background-color #F5F5F6

            .head-item
                display flex
                flex-direction column
                align-items center
                justify-content center
                padding 0 5px
                width 20%
                height 100%

                .news-icon
                    display flex
                    justify-content center
                    align-content center
                    padding 8px
                    width 40px
                    height 40px
                    font-size 20px
                    border 2px solid #ccc
                    border-radius 50%

                .name
                    margin-top 0.5em
                    font-size 14px

        > .scroll-wrapper
            flex 1
            width 100%

            .new
                display flex
                padding 10px 15px

                .new-pic
                    width 33%
                    padding-top 33%
                    height 0
                    background-position center
                    background-repeat no-repeat
                    background-size cover

                .new-content
                    flex 1

                    .title
                        margin-bottom 10px
                        line-height 1.74625
                        font-size 18px

                    .info
                        display flex
                        justify-content space-between
                        line-height 1.74625

                        > div
                            font-size 14px

    .news-popover
        position absolute !important
        top 46px
        left 0
        right 0
        width 100%
        max-width 100% !important
        max-height 50vh !important
        box-shadow none !important

        .list .item
            padding 15px
            text-align center

            &.active
                color #2e7bff
</style>



